  .blog{
    margin-left: 6rem;
  }
  
  .blogcard {
    width: 750px;
    padding: 20px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    border: 1px solid rgba(255, 255, 255, 0.263);
    backdrop-filter: blur(10px) brightness(1.2);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    color: #ffffff;
    font-family: Arial, sans-serif;
    transition: all 0.3s ease-in-out;
    margin-left: 10rem;
    margin-top: 2rem;
  }
  
  .blogcard:hover {
    transform: skew(1deg, -1deg) scale(1.00);
  }
  
  
  .blogcard h1 {
    font-size: 1.5em;
    color: #ffffff;
    margin-bottom: 10px;
  }
  
  .blogcard p {
    font-size: 1em;
    color: #e0e0e0;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .blogcard a {
    display: inline-block;
    color: white;
    text-decoration: none;
    font-weight: bold;
    border-bottom: 2px solid transparent;
    transition: color 0.3s ease, border-bottom 0.3s ease;
    float: right;
    margin-top: -2rem;
    font-size: 2rem;
  }
  
  .blogcard a:hover {
    color: #ffffff;
    border-bottom: 2px solid purple;
  }
  
  .headerbar{
    display: flex;
    gap: 1rem;
    font-size: 1rem;
  }